<template>
<h1>welcome</h1>
<table class="center" style="background-color:green">
  
  <label for="name">HEADER</label>
  <input type="text" id="name" @input="(event) => (a = event.target.value)" />
  <label for="name">DATA TYPE</label>
  <select v-model="t" id="name" placeholder="type">
    <option v-for="item in inptype" :key="item" v-bind:vlaue="item">
      {{ item }}
    </option>
  </select>

  <br />
  <button @click="fun()">ADD COLUMN</button>
  <button @click="del()">DELETE COLUMN</button>
</table>
  <table style="background-color: blue; float:left">
    <div v-for="items in ar" v-bind:key="items">
      <label for="name">{{ items }}</label>
      <input
        v-bind:type="tr[items]"
        id="name"
        @input="(event) => (d[items] = event.target.value)"
      />
    </div>
    <button v-on:click="addd()">ADD ROW</button>
    <button @click="br.pop()">DELETE ROW</button>
  </table>

  <table style="float:right; background-color:pink">
    <label for="name">Index</label>
    <input
      type="number"
      id="name"
      @input="(event) => (n = event.target.value)"
    />
    <br />
    <label for="name">COLUMN</label>
    <select v-model="c" id="name">
      <option v-for="item in ar" :key="item" v-bind:vlaue="item">
        {{ item }}
      </option>
    </select>
    <br />
    <label for="name">New Value</label>
    <input
      v-bind:type="tr[c]"
      id="name"
      @input="(event) => (nv = event.target.value)"
    />
    <br />
    <button @click="editt()">edit</button>
  </table>

  <br>
  <div class="center" style="overflow-x: auto; width: 600px; height: 300px;float:center">
    <table class="center" style="overflow-x: auto; width: 600px; height: 300px;">
      <th>SNO.</th>
      <th v-for="x in ar" v-bind:key="x">{{ x }}</th>
      <tr v-for="items in br" v-bind:key="items">
        <td>{{ items.siz }}</td>
        <td v-for="t in ar" v-bind:key="t">{{ items[t] }}</td>
      </tr>
    </table>
  </div>
  <button @click="this.br = []">DROP</button>
  <button @click="downloadCSVData()">download</button>
</template>

<script>
export default {
  name: "card-1",
  data() {
    return {
      d: {},
      a: {},
      ar: [],
      br: [],
      t: {},
      tr: {},
      n: null,
      nv: null,
      c: null,
      inptype: [
        "number",
        "checkbox",
        "color",
        "date",
        "datetime-local",
        "file",
        "image",
        "month",
        "number",
        "password",
        "radio",
        "range",
        "reset",
        "search",
        "submit",
        "tel",
        "text",
        "time",
        "url",
        "week",
      ],
    };
  },
  mounted() {
    if (localStorage.br) {
      this.br = JSON.parse(localStorage.br);
    }
    if (localStorage.ar) {
      this.ar = JSON.parse(localStorage.ar);
    }
    if (localStorage.tr) {
      this.tr = JSON.parse(localStorage.tr);
    }
  },

  watch: {
    br: {
      handler(newbr) {
        localStorage.br = JSON.stringify(newbr);
      },
      deep: true,
    },

    ar: {
      handler(newar) {
        localStorage.ar = JSON.stringify(newar);
      },
      deep: true,
    },
    tr: {
      handler(newtr) {
        localStorage.tr = JSON.stringify(newtr);
      },
      deep: true,
    },
  },

  methods: {
    fun() {
      const t = Object.assign(this.t);

      this.ar.push(this.a);
      this.tr[this.a] = t;
    },
    del() {
      this.ar.pop();
    },
    addd() {
      const d = Object.assign({}, this.d);
      d["siz"] = this.br.length;
      this.br.push(d);
    },
    downloadCSVData() {
      let tags = this.br;
      let br = tags.map((tag) => Object.values(tag));
      br.splice(0, 0, this.ar);
      var array = br;

      var str = "";
      for (var i = 0; i < array.length; i++) {
        let line = "";
        line = array[i].join(",");
        str += line + "\r\n";
      }
      var blob = new Blob([str], { type: "text/csv;charset=utf-8;" });

      var link = document.createElement("a");
      var url = URL.createObjectURL(blob);
      link.setAttribute("href", url);
      link.setAttribute("download", "csvfilename.csv");
      link.style.visibility = "hidden";
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    editt() {
      if (this.n < this.br.length) {
        this.br[this.n][this.c] = this.nv;
      } else {
        alert("Index out of range");
      }
    },
  },
};
</script>

